"use strict";
exports.price = {
    read: function (amount) {
        var num;
        if (typeof amount !== 'number') {
            num = parseFloat(amount);
        }
        else {
            num = amount;
        }
        return (num / 100).toFixed(2);
    },
    write: function (amount) {
        var num = +amount.replace(/[^\d.]/g, '');
        return isNaN(num) ? 0 : parseFloat(num.toFixed(2)) * 100;
    },
};



