"use strict";
var Vue = require('vue');
var VueValidator = require('vue-validator');
// We need to register the validator before we import app components.
Vue.use(VueValidator);
var App = require('./components/app/app.vue');
require('./main.styl');
require('hint.css/hint.base.css');
var vue = new Vue({
    el: 'body',
    components: {
        App: App,
    },
});



